.placeholder {
  min-height: 100vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 5%;
  padding-right: 5%;
}

.placeholder .left {
  opacity: 0;
  transform: translateY(50px);
  transition: opacity 0.9s ease, transform 0.6s ease;
  display: flex;
  flex-direction: column;
  align-items: baseline;
  max-width: 45%;
}

.placeholder .left.fade-in {
  opacity: 1;
  transform: translateY(0);
}

.placeholder .left .title1 {
  font-size: 3.4rem;
  font-weight: 750;
}

.placeholder .left .title2 {
  font-size: 2.3rem;
  font-weight: 400;
}

.placeholder .left p {
  text-align: left;
  font-size: 1.5rem;
  font-weight: 400;
  margin-top: 2rem;
  margin-bottom: 0.5rem;
}

.placeholder .left button {
  border: none;
  background-color: #f4d35e;
  color: black;
  padding-top: 2rem;
  padding-bottom: 2rem;
  padding-left: 4.5rem;
  padding-right: 4.5rem;
  font-size: 2rem;
  border-radius: 0.5rem;
  cursor: pointer;
}

.placeholder .left button:hover {
  background-color: #fff2c2;
  transition: all 0.5s;
}

.placeholder .left .actions {
  margin-top: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.placeholder .right {
  opacity: 0;
  transform: translateY(50px);
  transition: opacity 0.9s ease, transform 0.6s ease;
}

.placeholder .right img {
  height: 40rem;
}

.placeholder .right.fade-in {
  opacity: 1;
  transform: translateY(0);
}
