.home {
  min-height: 85vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 5%;
  padding-right: 0;
}

.left{
    display: flex;
    flex-direction: column;
    align-items: baseline;
    max-width: 35%;
}

.left .title1{
    font-size: 3.4rem;
    font-weight: 750;
    color: #15D6CD;
}

.left .title2{
    font-size: 2.3rem;
    font-weight: 400;
    color: #EDEDED;
}

.left p{
    text-align: left;
    font-size: 1.5rem;
    font-weight: 400;
    color: #EDEDED;
}

.left button{
    border: none;
    background-color: #F4D35E;
    color: black;
    padding-top: 1rem;
    padding-bottom: 1rem;
    padding-left: 4.5rem;
    padding-right: 4.5rem;
    font-size: 2rem;
    border-radius: .5rem;
    cursor: pointer;
}

.left button:hover{
    background-color: #fff2c2;
    transition: all .5s;
}

.left a{
    font-size: 1.7rem;
    color: #15D6CD;
}

.left .actions{
    margin-top: 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.left img{
    height: 2rem;
    margin-left: 2rem;
    cursor: pointer;
}

.right{
}

.right img{
    height: 40rem;
}